.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 56px;

  color: #1d1d1d;
  background: #fff;
  border-radius: 40px;
  font-weight: 700;
  font-size: 16px;

  cursor: pointer;
  transition: box-shadow 0.5s;

  &:hover {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.4);
  }

  &:active {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.64);
  }
}
